<template>
	<div class="goods_box">
		<div class="search_box">
			<header>
				<Haednav head_left="" head_title='订单列表' head_right=" ">
					<router-link to="/cart" slot="left" tag="span"><van-icon name="arrow-left" /></router-link>
				</Haednav>
			</header>
			<div class="goods_cont">
				<ul>
					<li @click="affirm(item)" v-for="(item,index) in list" :key="index">
						<img :src="`//elm.cangdu.org/img/`+item.image_path" alt="">
						<div class="goods_right">
							<div class="goods_text">
								<div>
									<b>{{item.name}}</b>
									<p>{{item.server_utc.slice(0,16).replace('T','/')}}</p>
								</div>
								<p>等待支付</p>
							</div>
							<div class="goods_name">
								<p>{{item.description}}</p>
								<b v-for="(val,i) in item.specfoods.slice(0,1)">
									￥{{item.num*val.price}}.00
								</b>
							</div>
							<p class="goods_tim">去支付(还剩下14分50秒)</p>
						</div>
					</li>
				</ul>
			</div>
			<footer>
				<Foot-Nav></Foot-Nav>
			</footer>
		</div>
	</div>
</template>

<script>
	import Haednav from '../../About/headNav'
	import FootNav from '../../About/footNav'
	export default {
		name: "goods",
		data(){
			return{
				list:[]
			}
		},
		components:{
			Haednav,
			FootNav,
		},
		computed:{
			allNum(){
				let data = [...this.list]
				let Allnum = 0
				for(let i=0;i<data.length;i++){
					Allnum+=data[i].num
				}
				return Allnum
			}
		},
		created(){
			this.list = JSON.parse(localStorage.getItem('addShop'))
			console.log(this.list)
		},
		methods:{
			affirm(item){
				this.$store.commit('appShop',item)
				this.$router.push({path:'/affirm'})
			}
		}
	}
</script>

